.classcomponent {
    text-align: center;
    background-color: rgba(0, 89, 255, 0.418);
    width: 250px;
    margin: auto;
    margin-top: 5%;
    padding: 5px 5px;
    border: 3px solid black;
    border-radius: 20px;
    
}

.classcomponent label{
    padding: 10px 10px;
    font-weight: bold;
    font-size: 20px;
}

.classcomponent button{
    padding: 5px 5px;
    margin: 5%;
    border-radius: 8px;
    font-weight: bold;
    font-size: 25px;
    width: 30px;
}