.Labourcontainer {
  display: flex;
  align-items: center;
  /* justify-content: center; */
  justify-content: space-evenly;
  /* margin: 50px 50px 50px 50px; */
}

.Carddetails {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 5px;
  height: 60px;
  font-size: 22px;
}

.Cardimage {
  margin: 0px;
  padding: 0px;
}

.Carddetails div {
  margin-left: 10px;
  justify-content: center;
}

.Labourdetails h1 {
  text-align: center;
  border-style: double;
  border-radius: 10px;
  border-color: rgb(165, 42, 42);
  border-width: 6px;
}

.Labourtable {
  border-collapse: collapse;
}

.Labourtable th {
  background-color: yellowgreen;
}

.Carddetails a, .Carddetails a button{
  cursor: pointer;
}
