.card {
    width: 308px;
    background-color: #7422618f;
    border-radius: 5px;
    margin: 40px;
    border: 1px solid black;
}

.card:hover {
    box-shadow: 2px 2px 10px black;
}

.image {
    width: auto;
    text-align: center;
    padding: 10px;
    position: relative;
}

image.img {
    max-width: 50%;
    height: auto;
}

.title {
    text-align: center;
    padding: 1px;
    width: 50%;
}

.empinfo {
    display: flex;
    align-items: center;
}

.button {
    margin-top: 30px;
    margin-bottom: 30px;
    background-color: white;
    border: 1px solid black;
    padding: 5px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 15px;
    background: transparent;
    border: none;
}

.button .btn
{
   /* background: transparent;
    border: none;*/
    margin: 3px 86px 2px 43px;
    width: 47%;
    padding: 2px;
}

.btn:hover 
{
    transition: .5s;
    background-color: green;
    color: white;
}
