        .caldata {
            /* border: 1px solid black;
			margin-left: auto;
            margin-right: auto;
			margin: 0 auto;
            display: inline;*/
            text-align: center;
            background-color: yellow;
            width: 30%;
            margin: auto;
            margin-top: 1%;
            padding: 2px 0px;
            border: 2px solid black;
            border-radius: 15px;
        }

        .caldata h1{
            text-align: center;
            width: 100%;
        }

        .calbtn {
            width: 100%;
            background-color: yellow;
            color: white;
            font-size: 24px;
            border-radius: 5px;
            margin:5px 0px;
            padding: 0px 0px;
        }

        .calbtn button{
            width: 20%;
            padding: 2px 2px;
            background-color: rgb(2, 67, 2);
            color: white;
            font-size: 24px;
            border-radius: 5px;
            margin:2px 2px;
        }

        .calcontainer .caltxt {
            padding: 10px 40px;
            font-size: 24px;
            border-radius: 5px;
            border: 2px solid black;
            width: 62%;
        }

        .calcontainer{
            width: 100%; 
        }
        

        .calbtn button:hover {
            opacity: 1.8;
            background-color: red;
            color: black;
        }
        .crud{
            text-align: center;
            width: 30%;
            margin: auto;
            margin-top: 10px;
            padding: 10px 2px;
            border: 2px dashed black;
            border-radius: 10px;         
        }

    .crud button{
        width: 25%;
             padding: 3px 3px;
            background-color: red;
            font-size: 15px;
            font-weight: bold;
            border-radius: 5px;
             margin:2px 2px; 
    }