
.container
{
    
	background-color: rgb(159, 116, 230);
    width: 375px;
    margin: auto;
    margin-top: 12%;
    padding: 10px 10px;
    border: 2px solid black;
    border-radius: 40px;	
}

.H1
{
    text-align: center;
	padding: 2px;
}

container label
{
	margin-top: 20px;
	font-size: 18px;
	text-align:left;
	padding: 50px 30px;
}

container input
{
	width: 90%;
	padding: 10px;
	border: 1px solid gray;
	border-radius: 6px;
}

.btn
{
    margin: 20px 100px;
	width: 25%;
	padding: 5px;
}
.btn:hover
{
    opacity: 0.8;
	background-color: rgb(241, 245, 31);
	color: white;
}
.formCover{
	display: flex;
    margin: 0 0 0 0;
}

.formCover > div{
	display: inline-block;
	width: 50%;
}
.formCover input {
	display: inline-block;
	width: 50%;
}
.sumbitButtomCover{
	text-align: center;
}