.Labourtable,
thead,
tr {
  border: 1px solid black;
  border-collapse: collapse;
  vertical-align: center;
  padding: 10px;
  font-weight: bold;
}

.Pagination button:hover {
  background-color: red;
  color: white;
}

.Pagination {
  display: inline-block;
}

.Pagination button {
  font-weight: bold;
  font-size: 17px;
  color: black;
  float: left;
  padding: 2px 2px;
}
