.Labourcard{
    background-color: rgba(21, 177, 239, 0.46);
    display: inline-block;
    margin:5px;
    padding:2px;
    padding-top:-10px;
    text-align: center;
    border: 1.6px solid rgb(0, 0, 0);
    border-radius: 10px;
    font-weight: bold;
}

.Cardimage{
    margin:1px;
    padding:10px;
    border-radius: 10px;    
   /* background-color: rgb(56, 24, 236);*/
}

.Cardimage image{
    margin:1px;
    padding:5px;
    border: 1px solid rgb(0, 0, 0);
}


.Carddetails button:hover{

    transition: .5s;
    background-color: green;
    color: white;
    border-radius: 10px;

}

.Carddetails{
    background-color:yellow;
    margin:1px;
    padding:5px;
    /*border: 1px solid rgb(0, 0, 0);*/
    border-radius: 10px;
}

.Carddetails button{
    padding:5px;
    margin:4px;
    border-radius: 5px;
}

.Carddetails h3,p{
    margin:0px;
}